<template>
  <section>
    <section class="projects">
      <!-- FRONTEND -->
      <article>
        <h2><i class="fas fa-code"></i> Frontend</h2>
        <ul>
          <li v-for="(item, article) in projects.front" :key="article">
            <a target="_blank" :href="item.url">Link</a> - {{ item.title }}
          </li>
        </ul>
      </article>
      <!-- FRONTEND -->

      <!-- DESIGN -->
      <article>
        <h2><i class="fas fa-drafting-compass"></i> Design</h2>
        <ul>
          <li v-for="(item, article) in projects.design" :key="article">
            <a
              @click="
                showModalDesign(
                  item.screen,
                  item.title,
                  item.description,
                  item.url
                )
              "
              >Modal</a
            >
            - {{ item.title }}
          </li>
        </ul>
      </article>
      <!-- DESIGN -->

      <!-- VIDEO -->
      <article>
        <h2><i class="far fa-play-circle"></i> Video</h2>
        <ul>
          <li v-for="(item, article) in projects.video" :key="article">
            <a @click="showModalVideo(item.url)">Watch</a> - {{ item.title }}
          </li>
        </ul>
      </article>
      <!-- VIDEO -->
    </section>

    <div class="modal-design" v-show="modalDesign">
      <figure>
        <img class="modal-image" />
      </figure>
      <div>
        <h2 class="modal-title"></h2>
        <p class="modal-description"></p>
        <div class="modal-buttons">
          <a id="modal-link" target="_blank" class="btn btn-primary"
            ><i class="fas fa-external-link-alt"></i> Ver projeto</a
          >
          <a @click="modalDesign = false" class="btn btn-secondary"
            ><i class="fas fa-times"></i> Fechar</a
          >
        </div>
      </div>
    </div>

    <div class="modal-video" v-show="modalVideo" @click="offModalVideo">
      <div class="modal-block">
        <iframe
          title="renato7oliveira no YouTube!"
          frameborder="0"
          height="500"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        ></iframe>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      modalDesign: false,
      modalVideo: false,

      projects: {
        front: [
          {
            title: "Tesla Header 2021",
            stack: "Flex-box",
            url: "https://renato7oliveira.github.io/experimentos/Tesla/src/",
          },
          {
            title: "Starbucks Nicepage",
            stack: "CSS-Grid / Flex-box",
            url: "https://renato7oliveira.github.io/experimentos/Starbucks/src/",
          },
          {
            title: "Template Code Canyon",
            stack: "CSS-Grid",
            url: "https://renato7oliveira.github.io/experimentos/Grid_CSS_Template_Canyon/src/",
          },

          {
            title: "Street Fighter Tributo",
            stack: "Adobe Edge Animate",
            url: "https://renato7oliveira.github.io/street-fighter-audio-project",
          },
          {
            title: "Beatles Tributo",
            stack: "CSS3 animate / Vue.js",
            url: "https://renato7oliveira.github.io/experimentos/Beatles_Tributo/src/",
          },
          {
            title: "E-mail Mkt 1.0",
            stack: "HTML 1.0",
            url: "https://renato7oliveira.github.io/experimentos/Email_Mkt/Vivo/index.html",
          },
        ],
        design: [
          {
            title: "PDV Cinemark (UI)",
            description: "Descrição projeto",
            url: "https://www.youtube.com/embed/HWgCY43AHxc",
            screen: require("../assets/images/500-800.jpg"),
          },
        ],
        video: [
          {
            title: "Game Play: KOF 96",
            url: "https://www.youtube.com/embed/HWgCY43AHxc",
          },
          {
            title: "Game Play: KOF 95",
            url: "https://www.youtube.com/embed/U8PxtB5k0J8",
          },
          {
            title: "Thug Life Bad Child",
            url: "https://www.youtube.com/embed/DJrxU-av8SI",
          },
          {
            title: "Breaking Bad - Slides Show",
            url: "https://www.youtube.com/embed/qHwl86jkmGg",
          },
        ],
      },
    };
  },

  methods: {
    showModalDesign(modalImage, modalTitle, modalDescription, modalUrl) {
      this.modalDesign = true;
      document.querySelector(".modal-image").src = modalImage;
      document.querySelector(".modal-title").innerHTML = modalTitle;
      document.querySelector(".modal-description").innerHTML = modalDescription;
      document.querySelector("#modal-link").setAttribute("href", modalUrl);
    },
    showModalVideo(url) {
      this.modalVideo = true;
      document.querySelector("iframe").src = url;
    },
    offModalVideo() {
      this.modalVideo = false;
      document.querySelector("iframe").src = "";
    },
  },
};
</script>

<style lang="scss" scoped>
$color-primary: #43627f;
$color-secondary: #21313f;
$background-hover: rgba(
  $color: #000,
  $alpha: 0.5,
);
$radius-def: 2px;
$border-def: rgba(
  $color: #ccc,
  $alpha: 0.5,
);

// PROJECTS START

.projects {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 25px;
  h2 {
    font-weight: 500;
    margin-bottom: 15px;
    i {
      font-size: 0.8em;
    }
  }
  li {
    font-size: 1.2em;
    line-height: 45px;
    &:hover {
      background: rgba($color: $color-primary, $alpha: 0.1);
      padding-left: 15px;
    }
    a {
      color: #a69632;
      cursor: pointer;
      text-transform: uppercase;
    }
  }
}

@media (max-width: 700px) {
  .projects {
    grid-template-columns: 1fr;
    margin: 25px;
  }
}

.head :nth-child(3) {
  text-align: center;
}

// PROJECTS END

// MODAL DESIGN START

.modal-design {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  align-content: center;
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  right: 0;
  background: #F5F6F8;
  position: fixed;
  z-index: 1;
  img {
    width: 100%;
    max-height: 100vh;
  }
  div {
    // display: grid;

    margin: 25px;
  }
  .modal-buttons {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 25px;
    margin: 25px 0;
  }
}
@media (max-width: 700px) {
  .modal-design {
    grid-template-columns: 1fr;
    .modal-buttons {
      grid-template-columns: 1fr;
      grid-gap: 15px;
    }
  }
}

// MODAL DESIGN END

// MODAL VIDEO START

.modal-video {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  align-items: center;
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  right: 0;
  background: #fafafa;
  position: fixed;
  z-index: 1;
  background: #000;
  .modal-block {
    grid-column: 2 / 7;
    padding-top: 0px;
    iframe {
      width: 100%;
    }
  }
}

// MODAL VIDEO END
</style>